.nav {
  color: #fff;
}

.nav__list {
  gap: 48px;
  display: flex;
}

.nav__item {
  text-transform: uppercase;
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 19.5px;
}

.nav__link {
  padding-block: 10px;
  position: relative;
}

.nav__link:after {
  content: "";
  transform-origin: 1px;
  z-index: 100;
  background-color: #fff;
  width: 100%;
  height: 1px;
  transition: transform .3s;
  display: block;
  position: absolute;
  bottom: 0;
  transform: scale(0);
}

.nav__link:hover:after {
  transform: scale(1);
}

.main-btn {
