}

.learn-btn:before {
  content: "";
  background-color: #2060f6;
  background-image: url("arrow-btn.c8f159eb.svg");
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 100%;
  width: 24px;
  height: 24px;
  padding: 8px;
  transition: transform .3s;
  display: block;
}

.learn-btn:hover:before {
  transform: scale(1.1);
}

.mobile-menu {
  z-index: 100;
  background-color: #6c788b;
  width: 320px;
  transition: transform .3s;
  position: fixed;
  top: 0;
  bottom: 0;
  transform: translateX(-320px);
}

.mobile-menu:target {
  transform: translateX(0);
}

.mobile-menu__close-icon {
  background-color: #fff;
  border-radius: 50%;
  display: block;
  position: absolute;
