    width: 109px;
    height: 44px;
  }
}

.testimonials-card__image:before {
  left: -35px;
}

@media (width >= 640px) {
  .testimonials-card__image:before {
    left: -47px;
  }
}

@media (width >= 1024px) {
  .testimonials-card__image:before {
    left: -35px;
  }
}

.testimonials-card__image:after {
  bottom: 0;
  left: 35px;
}

